<template>
  <div class="container">
    <calendar :name="motto"   @changeName="getName"></calendar>
    <a href="../number/main">跳转</a>
    <div>点击获取子组件传递过来的值</div>

    <div class="view-tab">
      <ul>
        <li v-for="(item,index) in tabList" :key="index" @click="setTab(index)">
          <!-- class的三元表达式写法 -->
           <div :class="[index == currIndex ? 'mycolor':'',view-list]"> {{item.id}}</div>
           <!-- style的三元表达式写法 -->
           <!-- <div :style="{ backgroundColor:currIndex==index ? '#f40':' '}" class="view-list">{{item.id}}</div> -->
        </li>
      </ul>
    </div>

    <input type="text" :value='motto'>
    <button @click="changeVal">按钮</button>
  </div>
</template>

<script>
import card from "@/components/card";
import calendar from "@/components/calendar";
import { get, post } from "@/utils/network";

export default {
  data() {
    return {
      tabList: [],
      motto: "Hello World",
      userInfo: {},
      currIndex: 0
    };
  },

  components: {
    card,
    calendar
  },

  methods: {
    getName(msg) {
      console.log(msg);
    },
    setTab(e) {
      this.currIndex = e;
      console.log(this.currIndex);
    },
    changeVal() {
      this.motto = "还可以";
    }
  },
  created() {
    get("banner/list", { key: "mallName" }).then(res => {
      console.log(res);
      this.tabList = res.data;
    });
    
  }
};
</script>

<style scoped lang="less">
.view-tab {
  width: 80%;
  margin: 100rpx auto;
  height: 500rpx;
  border: 1px solid #ccc;
  text-align: center;
  font-size: 28rpx;
  ul {
    width: 100%;
    height: 50rpx;
    li {
      width: 25%;
      height: 100%;
      border-right: 1px solid #fff;
      float: left;
      background-color: #ccc;
      box-sizing: border-box;
      .view-list {
        height: 100%;
      }
    }
    .mycolor {
      background-color: #f40;
      height: 100%;
    }
  }
}
</style>



